export default {
  name: "Dialog",
  data() {
    return {
      optionwater: {
        mode: 'fill',
        textBaseline: 'middle',
        font: '12px Arial',
        fillStyle: 'white',
        content: '@willinthontech',
        rotate: -45
      }
    };
  },
  methods: {
    filter() {
      this.$emit("SendDialog", this.optionwater);
      this.$emit("CloseDialog");
    },
  },
};
